import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';

import { LoginService } from "../../services/login/login.service";
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { LocalStorageService } from "../../services/local-storage/local-storage.service";
import { ErrorService } from "src/app/services/errors/error.service";

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  constructor(
    private fb: FormBuilder, private loginService: LoginService, private router: Router,
    private spinner: NgxSpinnerService, private localStorageService: LocalStorageService,
    private errorService: ErrorService
  ) { }

  // Variables
  loginForm = this.fb.group({
    email: ['', Validators.required],
    password: ['', Validators.required]
  });

  errorMsg: string = '';

  ngOnInit(): void {
  }

  clearMessage() {
    this.errorMsg = '';
  }

  login() {
    this.clearMessage();
    this.spinner.show();

    let loginObj = {
      email: this.loginForm.get('email').value,
      password: this.loginForm.get('password').value
    }
    this.loginService.login(loginObj).subscribe((loginRes: any) => {

      console.log('login res', loginRes);
      if (loginRes.status === true) {
        let user = loginRes.responseObj;
        console.log(loginRes.message);
        
        if (user.User.rollAs === 'Admin') {
          this.spinner.hide();
          this.localStorageService.setUserData(user);
          this.router.navigate(['/core']);
        }
        // else{
        //   this.spinner.hide();
        //   this.localStorageService.setUserData(user);
        //   this.router.navigate(['/core']);
        // }
      } else {
        this.spinner.hide();
        this.errorMsg = loginRes.message;
      }
    }, (httpError) => {
      this.spinner.hide();
      this.errorMsg = this.errorService.errorMsg;
    });
  }

}
